<template>
<div class="list">
  <div @click="openPicker" class="date">日期：{{ defaultDate }}</div>
  <div class="flex_box">
    <div class="search">
      <i class="iconfont search_icon"></i>
      <input type="text" placeholder="请输入需要查找的车牌" v-model="keyword">
      <i class="iconfont close_icon" v-if="keyword" @click="keyword=''"></i>
    </div>
    <div class="btn_box">
      <mt-button type="primary" size="small" @click="search">搜索</mt-button>
    </div>
  </div>
  <ul
  v-infinite-scroll="loadMore"
  infinite-scroll-disabled="loading"
  infinite-scroll-distance="50"
  >
    <li v-for="(item, i) in list" :key="i">
      <div>地点：{{item.a}}</div>
      <div>签到时间：{{item.b}}</div>
      <div>车牌：{{item.b}}</div>
    </li>
  </ul>
  <mt-datetime-picker
    v-model="pickerVisible"
    ref="picker"
    type="date"
    :startDate="startDate"
    :endDate="endDate"
    @confirm="handleConfirm"
    year-format="{value} 年"
    month-format="{value} 月"
    date-format="{value} 日">
  </mt-datetime-picker>
  <div class="footer">
    <div>
      合计：2车次
    </div>
  </div>
</div>
</template>
<script>
import { formatDate } from '@/services/utils'
export default {
  data () {
    return {
      keyword: '',
      defaultDate: '',
      pickerVisible: new Date(),
      startDate: new Date('1970-1-1'),
      endDate: new Date(),
      list: [
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        }
      ]
    }
  },
  created () {
    this.defaultDate = formatDate()
  },
  methods: {
    // 搜索
    search () {
    },
    // 日期选择
    openPicker () {
      this.$refs.picker.open()
    },
    // 确定日期选择
    handleConfirm (data) {
      this.defaultDate = formatDate(data)
    },
    // 下拉加载
    loadMore () {
      this.loading = false
      let a = [
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        }
      ]
      this.list = [...this.list, ...a]
    }
  }
}
</script>
<style lang="scss" scoped>
.list {
  height: 100%;
    > div:not(.date) {
      height: 40px;
      box-sizing: border-box;
    }
    ul {
      height: calc(100% - 120px);
      overflow: auto;
      border-top: 1px solid #eee;
    }
    li {
    padding: 15px;
    border-bottom: 1px solid #eee;
      > div {
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    li:last-child {
      border-bottom: none;
    }
  }
}
.date {
  padding: 0 15px;
  border-bottom: 1px solid #eee;
  line-height: 40px;
}
.flex_box {
  display: flex;
  align-items: center;
  padding: 0 15px;
  margin: 15px 0;
  .search {
    border: 1px solid #eee;
    margin-right: 10px;
    padding: 5px;
    box-sizing: border-box;
    border-radius: 5px;
    flex: 1;
    i {
      width: 20px;
      height: 40px;
      color: #999;
    }
  }
  input {
    border: none;
    width: calc(100% - 50px);
  }
}
.footer {
  position: fixed;
  bottom: 0;
  border-top: 1px solid #eee;
  width: 100%;
  background: #fff;
  > div {
    width: 100%;
    display: block;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
}
</style>



